<template>
    <div class="card" style="height: 95%;">
        <div class="card__title">
            <b-row>
            <b-col>
                <span>Generate Invoice (Customer)</span>
            </b-col>
            <b-col>
              <div class='Plan-Header-Bagde'>
              <span> Total : {{M_TotalCustomerAmt}} </span>
              </div>
            </b-col>
            <!-- <b-col style="max-width: max-content !important;" class="col-right" offset="4"> -->
            <b-col style="text-align: right;">  
                <span>
                  <ABSButton
                    :text="'Rental Billing (' + BillingRental + ')'"
                    classButton="button button--back"
                    classIcon="icon-style-1"
                    @click="doRentalBilling"
                  />
                </span>
                <span>
                  <ABSButton
                    :text="'Print'"
                    classButton="button button--back"
                    classIcon="icon-style-1"
                    @click="doPrint"
                  />
                </span>
                <span>
                  <ABSButton
                    :text="'View All'"
                    classButton="button button--back"
                    classIcon="icon-style-1"
                    @click="onViewAll"
                  />
                </span>
            </b-col>
            </b-row>
        </div>
        <div class="card__body">
          <div>
              <ACCFormList
                  :prop="propList"
                  :title="''"
                  @rowClicked="rowClicked"
                  @onRenderData="onRenderData"
                  ref="ref_completed_order_customer"
              >
                <template slot="total_order" slot-scope="data">
                  <div
                    :class="'Plan-Dot-Primary'"
                    :style="'font-size: 10px !important; padding-top: 9px !important;'"
                    >
                      {{data.item.total_order}}
                  </div>
                </template>
                <template slot="total_amount" slot-scope="data">
                  <span>{{isCurrency(data.item.total_amount, decimal)}}</span>
                </template>
              </ACCFormList>
          </div>
        </div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      M_TotalCustomerAmt: '0',
      BillingRental: 0,
      search: "",
      propList: {
        OptionUrl: "/ST/ST_CompletedOrderCustomer",
        LineNo: 0,
        initialWhere: "ss_portfolio_id='" + this.getDataUser().portfolio_id + "'",
        OrderBy: "customer_name",
        SourceField: "",
        ParamView: "",
        PerPage: 8,
      },
    };
  },
  computed: {},
  methods: {
    rowClicked(record, index) {
      var param = record;
      // param.isPrint = true;
      this.$store.commit("setParamPage", param);
      this.$router.push({ name: "ST_CompletedOrderCustomerView" });
    },
    RenderData() {
      this.$refs.ref_completed_order_customer.doGetList("");
    },
    doRentalBilling() {
      this.$router.push({ name: "ST_RentalBilling" });
    },
    doPrint() {
      this.$router.push({ name: "ST_CompletedOrderCustomerPrint" });
    },
    onViewAll() {
      this.$router.push({ name: "ST_CompletedOrderCustomer" });
    },
    onRenderData(data) {
      if (!data) return
      if (data[0].total_customer_amount && data[0].total_customer_amount !== '') {
        this.M_TotalCustomerAmt = this.isCurrency(data[0].total_customer_amount, this.decimal)
      }
    },
    GetCntRentalBilling() {
        var param = {
            option_function_cd: "GetSumSchRental",
            module_cd: "ST",
            ss_portfolio_id: this.getDataUser().portfolio_id,
            user_id: this.getDataUser().user_id
        };
        
        this.CallFunction(param).then(response => {
            if (response == null) return
            var data = response.Data[0];
            
            this.BillingRental = data.fop_sum_sch_rental && data.fop_sum_sch_rental !== '' ? data.fop_sum_sch_rental : 0;
        })
    }
  },
  mounted() {
    this.RenderData();
    this.GetCntRentalBilling();
  },
};
</script>

<style scoped>
</style>